import React, { useState } from "react";
import styled from "styled-components";
import profile from "assets/images/common/profile.jpg";

const Title = styled.h1`
  padding: 8% 0 35px;
  font-size: 40px;
  color: #000;
  font-weight: bold;
  @media screen and (max-width: 720px) {
    padding: 15% 0 15px;
    font-size: 25px;
  }
`;

const Wrap = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  width: 100%;
  max-width: 1000px;
  @media screen and (max-width: 720px) {
    align-items: center;
    flex-direction: column;
  }
`;

const Profile = styled.div`
  width: 30%;
  padding: 4% 0 0;
  @media screen and (max-width: 720px) {
    width: 40%;
    max-width: 200px;
  }
`;

const Img = styled.img`
  width: 100%;
  height: auto;
  object-fit: contain;
`;

const TabWrap = styled.div`
  width: 60%;
  @media screen and (max-width: 720px) {
    width: 100%;
    max-width: 600px;
    padding: 0 16px;
  }
`;

const TabMenu = styled.ul`
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
`;

const Tab = styled.li`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 50%;
  padding: 15px;
  font-size: 19px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: bold;
  transition: color 0.5s;
  cursor: pointer;
  box-sizing: border-box;
  &.focused {
    color: #000;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 3px;
      background-color: #000;
    }
  }
  @media screen and (max-width: 720px) {
    padding: 10px;
    margin: 10px 0 0;
    font-size: 14px;
  }
`;

const Content = styled.div`
  width: 100%;
  padding: 25px 0 0;
  @media screen and (max-width: 720px) {
    padding: 20px 0 0;
  }
`;

const Item = styled.div`
  display: flex;
  &:not(:last-of-type) {
    padding: 0 0 14px;
    @media screen and (max-width: 720px) {
      padding: 0 0 8px;
    }
  }
`;

const ItemLeft = styled.div`
  width: 30%;
  font-size: 14px;
  color: #666;
  text-align: left;
  @media screen and (max-width: 720px) {
    font-size: 11px;
  }
`;

const ItemRight = styled(ItemLeft)`
  width: 70%;
`;

export default function About() {
  const [currentTab, clickTab] = useState(0);

  const tabList = [
    { id: 0, name: "PROFILE", state: "profile" },
    { id: 1, name: "SKILLS", state: "skills" },
  ];

  const selectMenuHandler = (index) => {
    clickTab(index);
  };

  return (
    <>
      <Title>ABOUT</Title>
      <Wrap>
        <Profile>
          <Img src={profile} alt="profile" />
        </Profile>
        <TabWrap>
          <TabMenu>
            {tabList.map((el, index) => (
              <Tab
                key={index}
                className={index === currentTab ? "submenu focused" : "submenu"}
                onClick={() => selectMenuHandler(index)}
              >
                {el.name}
              </Tab>
            ))}
          </TabMenu>
          {tabList[currentTab].state === "profile" ? (
            <Content>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight></ItemRight>
              </Item>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight>1996 8 6</ItemRight>
              </Item>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight></ItemRight>
              </Item>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight>  </ItemRight>
              </Item>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight>
                  2017 02 ~ 2017 06    UXUI
                   ( , ) 
                </ItemRight>
              </Item>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight>
                  2017 07 ~ 2019 05 ""   
                  <br />
                  2019 08 ~ 2019 12 ""  
                  
                  <br /> 2020 10 ~ 2023 3 ""  
                  
                </ItemRight>
              </Item>
            </Content>
          ) : (
            <Content>
              <Item>
                <ItemLeft>HTML</ItemLeft>
                <ItemRight>
                      /     / HTML5
                  
                </ItemRight>
              </Item>
              <Item>
                <ItemLeft>CSS</ItemLeft>
                <ItemRight>
                  CSS3  / SCSS  / styled-components 
                </ItemRight>
              </Item>
              <Item>
                <ItemLeft>JavaScript</ItemLeft>
                <ItemRight>
                  JavaScript   /  
                </ItemRight>
              </Item>
              <Item>
                <ItemLeft>React</ItemLeft>
                <ItemRight>React   /  </ItemRight>
              </Item>
              <Item>
                <ItemLeft>Cross Browsing</ItemLeft>
                <ItemRight>
                  ie, chrome, firefox, safari, opera    
                  
                </ItemRight>
              </Item>
              <Item>
                <ItemLeft> </ItemLeft>
                <ItemRight>   </ItemRight>
              </Item>
              <Item>
                <ItemLeft> </ItemLeft>
                <ItemRight>    </ItemRight>
              </Item>
              <Item>
                <ItemLeft>Adobe</ItemLeft>
                <ItemRight>Photoshop, Illustrator   </ItemRight>
              </Item>
              <Item>
                <ItemLeft></ItemLeft>
                <ItemRight>  </ItemRight>
              </Item>
            </Content>
          )}
        </TabWrap>
      </Wrap>
    </>
  );
}
